html,
div,
header,
h1,
{
	margin: 0;
	padding: 0;
}
body{
	margin: 0;
}

html{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

*, *:before, *:after{
	-webkit-box-sizing: inherit;
	        box-sizing: inherit;
}

.header{
	position: fixed;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
	background-color: #5898e8;
	color: #fff;
	height: 56px;
	width: 100%;
}
.header i{
	vertical-align: middle;
}

.header .main-title{
	-ms-flex: 1 1 20%;
	    flex: 1 1 20%;
	padding: 10px 18px;
	letter-spacing: 0.5px;
	font-size: 15px;
	line-height: 22px;
	font-weight: 900;
}

.header .search-form-container{
	-ms-flex: 2 1 50%;
	    flex: 2 1 50%;
}
.search-form-container input{
	width: 100%;
	outline: 0 none;
	border: 0 none;
	height: 36px;
	border-radius: 2px;
	font-size: 14px;
	padding: 0 16px;
}
.header .cart{
	-ms-flex: 1 1 30%;
	    flex: 1 1 30%;
	text-align: center;
	font-size: 18px;
	font-weight: 500;
	letter-spacing: .1px;
}
.cart .cart-icon{
	position: relative;
	padding: 10px;
	font-size: 25px;
	cursor: pointer;
}
.cart .cart-icon .cart-count{
	position: absolute;
	right: 2px;
	top: 3px;
	width: 18px;
	height: 18px;
	background-color: #ff6161;
	border-radius: 50%;
	padding: 1px;
	font-size: 15px;
	text-align: center;

}


.container{
	width: 100%;
	padding-top: 66px;
}

.book-list{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
}

.book-list-item{
	width: 20%;
	padding: 10px;
	margin: 0.5rem;
	background-color: #fff;
	text-align: center;
	-webkit-box-shadow: 0px 1px 2px 1px #e1e1e1;
	        box-shadow: 0px 1px 2px 1px #e1e1e1;

}

.book-list-item img{
	width: 60%;
	height: 60%;
}
.book-list-item .book-price{
	font-size: 16px;
    font-weight: 500;
    color: #212121;
}
.book-list-item .book-price i{
	font-size: 14px;
}

.book-list-item .cart-button{
	padding: 0.5rem;
	width: 80%;
	background-color: #5898e8;
	color: #fff;
	outline: none;
	border: none;
	cursor: pointer;
}
.in-cart{
	background-color: #ff6161 !important;
	cursor: not-allowed !important;
}

.cart-container{
	position: fixed;
	right: -100%;
	top: 56px;
	height: 100vh;
	z-index: 9999;
	width: 380px;
	border-left: 0.5px solid gray;
	background-color: #fff;
	-webkit-transition: all .35s ease-in-out;
	-o-transition: all .35s ease-in-out;
	transition: all .35s ease-in-out;
	-webkit-box-shadow: 0px 1px 2px 1px #e1e1e1;
	        box-shadow: 0px 1px 2px 1px #e1e1e1;

}

.cart-open{
	right: 0;
}

.cart-list{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	padding: 5px;
}
.cart-list .cart-details{
	max-height: 30px;
	font-size: 16px;
    font-weight: 500;
    color: #212121;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.cart-list .cart-details .cart-total i{
	font-size: 14px;
}
.cart-list hr{
	width: 100%;
	background-color: gray;
}
.cart-list .cart-list-item{
	position: relative;
	display: -ms-flexbox;
	display: flex;
	background-color: #fff;
	padding: 10px;
	margin: 2.5px 0px;
	-webkit-box-shadow: 0px 1px 2px 1px #e1e1e1;
	        box-shadow: 0px 1px 2px 1px #e1e1e1;
	max-height: 100px;
}

.cart-list .cart-list-item img{
	height: 70px;
	width: 70px;
}

.cart-list .cart-list-item .cart-item-content{
	padding: 0 0 0 10px;
}
.cart-list .cart-list-item .cart-item-content .cart-item-price{
	font-size: 16px;
    font-weight: 500;
    color: #212121;
}
.cart-list .cart-list-item .cart-item-content .cart-item-price i{
	font-size: 14px;

}

.cart-list .cart-list-item .cart-item-content p{
	margin: 0;
	margin-bottom: 5px;
}
.cart-list .cart-list-item .cart-remove-btn{
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
	height: 28px;
	width: 28px;
	font-size: 25px;
	padding-top: 3px;
	text-align: center;
	border-radius: 50%;
	color: #ff6161;
}
.cart-list .cart-list-item .cart-quantity{
	display: -ms-flexbox;
	display: flex;
}
.cart-list .cart-list-item .cart-quantity button{
	outline: 0 none;
	border: 0 none;
	cursor: pointer;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	border: 1px solid #c2c2c2;
	background: -webkit-gradient(linear,left top, left bottom,from(#fff),to(#f9f9f9));
	background: -webkit-linear-gradient(#fff,#f9f9f9);
	background: -o-linear-gradient(#fff,#f9f9f9);
	background: linear-gradient(#fff,#f9f9f9);
	font-size: 16px;
	line-height: 1;
}
.cart-list .cart-list-item .cart-quantity div{
	width: 46px;
    height: 28px;
    text-align: center;
    padding: 3px 6px;
    border-radius: 2px;
    background-color: #fff;
    border: 1px solid #c2c2c2;
    margin: 0 9px;
}


